<template>
<div class="TVChartContainer" :id="containerId" />
</template>

<script>

import Vue from 'vue'
import './charting_library.min';
import Datafeed from './api/index.js';
import datasetpicker from '../global/configbuilder/datasetpicker'

function getLanguageFromURL() {
  const regex = new RegExp('[\\?&]lang=([^&#]*)');
  const results = regex.exec(window.location.search);
  return results === null ? null : decodeURIComponent(results[1].replace(/\+/g, ' '));
}
// export const tvBus = new Vue();
let border1Current, border2Current;
export default {
  name: 'TVChartContainer',
  props: {
    symbol: {
      default: 'Poloniex:BTC/USD',
      type: String,
    },
    interval: {
      default: '1',
      type: String,
    },
    containerId: {
      default: 'tv_chart_container',
      type: String,
    },
    datafeedUrl: {
      default: 'https://demo_feed.tradingview.com',
      type: String,
    },
    timezone: {
      default: 'Europe/Moscow',
      type: String,
    },
    libraryPath: {
      default: '/charting_library/',
      type: String,
    },
    chartsStorageUrl: {
      default: 'https://saveload.tradingview.com',
      type: String,
    },
    chartsStorageApiVersion: {
      default: '1.1',
      type: String,
    },
    clientId: {
      default: 'tradingview.com',
      type: String,
    },
    userId: {
      default: 'public_user_id',
      type: String,
    },
    fullscreen: {
      default: false,
      type: Boolean,
    },
    autosize: {
      default: true,
      type: Boolean,
    },
    studiesOverrides: {
      type: Object,
    },

    config: {
      default: {},
      type: [ Object, Boolean ]
    },
    backtestResult: {
      type: [ Object, Boolean ]
    },
  },
  tvWidget: null,
  data: function () {
    return {
      //config: this.config
    }
  },
  watch: {
    config: function(config) {
      if(config && config.watch && config.watch.exchange && config.watch.asset && config.watch.currency){
        const symba = `${ config.watch.exchange.toUpperCase() }:${ config.watch.asset.toUpperCase() }/${ config.watch.currency.toUpperCase() }`; //'Poloniex:LTC/BTC'
        console.log(config);
        let candleSize = config && config.tradingAdvisor && config.tradingAdvisor.candleSize;
        let from = config && config.backtest && config.backtest.daterange && config.backtest.daterange.from && new Date(config.backtest.daterange.from).getTime();
        let to = config && config.backtest && config.backtest.daterange && config.backtest.daterange.to && new Date(config.backtest.daterange.to).getTime();
        this.reloadChart(symba, this.calculateInterval(candleSize), from, to);
      }
    },
    backtestResult: function(config) {
      this.drawBacktestResult(config);
    }
  },
  methods: {
    reloadChart(symbol, interval, dateFrom, dateTo) {
      const chart = this.tvWidget.chart();

      dateFrom = dateFrom / 1000;
      dateTo = dateTo / 1000;
console.log(dateFrom, dateTo);
      chart.removeAllShapes();
      this.drawTimeRangeBorders(chart, dateFrom, dateTo);

      this.tvWidget.setSymbol(symbol, interval, () => {
        if(dateFrom && dateTo && dateFrom < dateTo) {
          chart.setVisibleRange({ from: dateFrom, to: dateTo }, ()=>{
          });
          // if setSymbol IS called, borders created above will be erased, so simply redraw them here:
          this.drawTimeRangeBorders(chart, dateFrom, dateTo);
        }
      });
    },
    drawTimeRangeBorders(chart, dateFrom, dateTo){
      border1Current && chart.removeEntity(border1Current);
      border1Current = chart.createShape({time: dateFrom},
        {
          shape: 'vertical_line',
          overrides: {
            linewidth: 3,
            linecolor: '#FF0000'
          },
        });
      border2Current && chart.removeEntity(border2Current);
      border2Current = chart.createShape({time: dateTo},
        {
          shape: 'vertical_line',
          overrides: {
            linewidth: 3,
            linecolor: '#FF0000'
          },
        });
    },
    calculateInterval(candleSize){
      let ret = 'D';
      switch(candleSize) {
        case 1:
          ret = '1';
          break;
        case 60:
          ret = '60';
          break;
        case 1440:
          ret = 'D';
          break;
      }
      return ret;
    },
    calculateTimeframe(config){
      return "1234D"
    },
    drawBacktestResult(result) {
      console.log(result);
      if(false){
        // draw execution shapes (arrow w/text):
        _.each(result.trades, (trade)=>{
          if(trade.action === 'buy'){
            this.tvWidget.chart().createExecutionShape()
              .setText(`@${ trade.price } Buy ${ trade.amount }`)
              .setTooltip(`@${ trade.price } Buy ${ trade.amount }, Balance ${ trade.balance }`)
              .setTextColor("rgba(0,255,0,0.8)")
              .setArrowColor("#0F0")
              .setDirection("buy")
              .setTime(trade.date)
              .setPrice(trade.price);
          } else {
            this.tvWidget.chart().createExecutionShape()
              .setText(`@${ trade.price } Buy ${ trade.amount }`)
              .setTooltip(`@${ trade.price } Buy ${ trade.amount }, Balance ${ trade.balance }`)
              .setTextColor("rgba(255,0,0,0.8)")
              .setArrowColor("#F00")
              .setDirection("sell")
              .setTime(trade.date)
              .setPrice(trade.price);
          }
        });
      }
      if(false) {
        // draw simple icons:
        _.each(result.trades, (trade) => {
          if (trade.action === 'buy') {
            widget.chart().createShape({ time: trade.date },
              {
                shape: 'icon',
                lock: true,
                disableSelection: true,
                overrides: {
                  size: 20,
                  color: '#0f0',
                  icon: 0xf00c //f041 - pin
                },
              });
          } else {
            widget.chart().createShape({ time: trade.date },
              {
                shape: 'icon',
                lock: true,
                disableSelection: true,
                overrides: {
                  size: 20,
                  color: '#f00',
                  icon: 0xf00d
                },
              });
            /*widget.chart().createShape({ time: trade.date },
            {
              shape: 'flag',
              color: '#F00',
              backgroundcolor: '#F00',
              overrides: {
                linewidth: 3,
                linecolor: '#F00',
                color: '#F00',
                backgroundcolor: '#F00',
              },
            });*/
          }
        });
      }

      _.each(result.trades, (trade) => {
        if (trade.action === 'buy') {
          widget.chart().createShape({ time: trade.date },
            {
              shape: 'note',
              // lock: true,
              disableSelection: true,
              overrides: {
                // text: `@${ trade.price } Buy ${ trade.amount }, Balance ${ trade.balance }`,
                text: 'asdfasdfas',

                color: '#0f0',
                backgroundColor: '#0f0',
                markerColor: '#0f0',
              },
            });
        } else {
          widget.chart().createShape({ time: trade.date },
            {
              shape: 'note',
              // lock: true,
              disableSelection: true,
              overrides: {
                text: 'asdfasdfas',
                // text: `@${ trade.price } Sell ${ trade.amount }, Balance ${ trade.balance }`,
                color: '#f00',
                backgroundColor: '#f00',
                markerColor: '#f00',
              },
            });
        }
      });
    }
  },
  mounted() {
    const that = this;
    const widgetOptions = {
      symbol: this.symbol,
      // interval: '5',
      // interval: '1h',
      // interval: '30',
      // interval: '1',
      interval: '1',
      // interval: this.interval,
      timeframe: 'max',
      // timeframe: '1830d',
      // BEWARE: no trailing slash is expected in feed URL
      // datafeed: new window.Datafeeds.UDFCompatibleDatafeed(this.datafeedUrl),
      datafeed: Datafeed,
      container_id: this.containerId,
      timezone: this.timezone,
      library_path: this.libraryPath,
      locale: getLanguageFromURL() || 'en',
      disabled_features: ['use_localstorage_for_settings'],
      enabled_features: ['study_templates'],
      charts_storage_url: this.chartsStorageUrl,
      charts_storage_api_version: this.chartsStorageApiVersion,
      client_id: this.clientId,
      user_id: this.userId,
      fullscreen: this.fullscreen,
      autosize: this.autosize,
      studies_overrides: this.studiesOverrides,
    };
    const widget = window.TradingView.widget;
    const tvWidget = new widget(widgetOptions);
    window.widget = tvWidget;
    this.tvWidget = tvWidget;

    tvWidget.onChartReady(() => {
      console.log('Chart has loaded!');
      const button = tvWidget.createButton();
        //.attr('title', 'Click to show a notification popup')
        // .addClass('apply-common-tooltip')
        // .on('click', () => tvWidget.showNoticeDialog({
        //   title: 'Notification',
        //   body: 'TradingView Charting Library API works correctly',
        //   callback: () => {
        //     // eslint-disable-next-line no-console
        //     console.log('Noticed!');
        //   },
        // }));
    });
  },
  destroyed() {
    if (this.tvWidget !== null) {
      this.tvWidget.remove();
      this.tvWidget = null;
    }
  }
}

</script>

